.Card {
    margin: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow: auto;
}

.CardHeader {
    font-size: 20pt;
    font-family: 'Comfortaa', serif;
}

.CardImage {
    height: 350px;
    min-width: 100%;
}

.Card:hover .CardImage {
    height: 200px;
}

.CardContent {
    padding-left: 0.5em;
}

.CardSubtitle {
    padding-left: 1em;
}

.CardLink {
    text-decoration: none !important;
    color: black !important;
    text-align: center;
    word-wrap: break-word;
}

.CardLink p {
    color: inherit;
}

.GridCard {
    height: 450px;
    width: 350px;
    margin: 1em;
    overflow: hidden;
}

.CardHover {
    padding-top: 2em;
}

@media(max-width: 480px) {
    .GridCard {
        width: 300px;
    }
}

.PageCard {
    width: 600px;
}

a {
    text-decoration: none;
}


